import "./Square.css";
const Square = ({ coordinates }: { coordinates: number }) => {
  if (coordinates % 2 === 0)
    return (
      <div className="squares black-squares">
        <img alt="pawn" src="assets/images/bp.png" />
      </div>
    );
  else return <div className="squares white-squares"></div>;
};
export default Square;
